/// PageActivities, starting page intended on displaying recent activities of JUTC system
<script setup>
    import { ref } from 'vue'
    import { fetchReports } from '@/api'
    import ReportList from '@/components/ReportList'

    const reports = ref([])
    const is_more = ref(true)
    //ToDo: maybe limit to not more then 3 if not connected ? 
    fetchReports()
        .then(fetchedReports => {    
            reports.value = fetchedReports
        })
        .finally(() => is_more.value = false)


</script>

<template>   
    
    <report-list :reports="reports" :loading="is_more"></report-list>
</template>
