    <div class="px-8 py-4">
        <div>
            <h3 class="inline font-semibold" :title="report.instruction">                
                    {{ report.instruction }}
            </h3>            
            <span class="text-gray-500"> • </span>
            <time class="text-gray-500 text-sm" :title="report.created_at">
                <!-- <router-link :to="{ name: 'JUTC activities' }" class="hover:underline"> -->
                    {{ report.created_ago }}
                <!-- </router-link> -->
            </time>
        </div>
        <p class="whitespace-pre-wrap" v-text="report.result"></p>
            <p class="inline-block mt-2 text-pink-500">
            of {{ new Intl.NumberFormat().format(report.difference) }} JUTC
        </p>

            <p class="text-gray-500 text-sm">    stamped with: {{report.label}}
        </p>
    </div>
