import { initWallet, useWallet} from 'solana-wallets-vue'
import { useWorkspace } from '@/composables'
import { LedgerWalletAdapter } from '@solana/wallet-adapter-wallets'
    //PhantomWalletAdapter, 
    //SolflareWalletAdapter,
    //CoinbaseWalletAdapter,
    //TrustWalletAdapter,
const DEBUG = true

export const resetWallet = async () => {//async () => {
    const wallets = [
        new LedgerWalletAdapter(),
    ]    
    
    const { setWalletID, setButtonUsed, walletID, buttonUsed } = useWorkspace()

    initWallet({ wallets, autoConnect: true })
    

    try {
        const { disconnect, publicKey } = useWallet()
        await disconnect()
        setWalletID('')
        if (DEBUG) console.log("Inside 'resetWallet1','disconnect' performed, ID:", walletID.value, publicKey.value)
    } catch {
        if (DEBUG) console.log("Inside 'resetWallet2', failed to disconnect, ID:", walletID.value)
    }
    
    setWalletID('')
    setButtonUsed(0)
    if (DEBUG) console.log("Inside 'resetWallet3', setWalletID('') and setButtonUsed(0) performed", walletID.value, buttonUsed.value)


}