/// PageJungle, intended to display the state of Jungle system
/// activity flag, amount of JUTC on Escrow, maybe a number of transactions per hour..., current promotion settings, recent transaction
<script setup>
import { ref } from 'vue'
import { fetchJungleData, } from '@/api' 
import { JungleDisp, PromoDisp,} from '@/models'
import PromsList from '@/components/PromsList' 

const jData = ref(Object) //new JungleDisp()
const promos = ref([])
const is_more = ref(true) 
const recent = ref()

fetchJungleData()
    .then(fetched_data => { 
        jData.value = JungleDisp.cpy(fetched_data)
        recent.value = fetched_data.recent_report
        fetched_data.promotions.forEach(p => promos.value.push(new PromoDisp(p))) 
    })
    .finally(
        () => is_more.value = false
    )

 
</script>

<template>
    <div v-if="!is_more" class="text-gray-500 text-sm">
        <div class="px-8 py-4">
            <h3 class="inline font-semibold" >                
                state of accounts: 
                <p class="inline-block mt-2 text-pink-500"> 
                    {{jData.state}} 
                </p>
            </h3>  
        </div><div class="px-8 py-4 font-semibold">
            The recent action:
            <p class="inline-block mt-2 text-pink-500">                 
                {{recent.instruction}} 
            </p>
            <span class="text-gray-500"> • </span>
            <time class="text-gray-500 text-sm">
                {{ recent.created_at }}
            </time> 
        </div><div class="px-8 py-4 font-semibold"> 
            Number of tokens in Escrow:         
            <p class="inline-block mt-2 text-pink-500">                
                {{ new Intl.NumberFormat().format(jData.nb_left_tokens.toFixed(2)) }} <!-- it results in warning :-) -->
            </p>
        </div><div class="px-8 py-4 font-semibold"> 
            Number of operations executed:         
              <p class="inline-block mt-2 text-pink-500">                
                {{ new Intl.NumberFormat().format(jData.counter) }} 
            </p>
        </div><div class="px-8 py-4 font-semibold"> 
            Number of operations failed so far:         
              <p class="inline-block mt-2 text-pink-500">                
                {{ new Intl.NumberFormat().format(jData.counter_failures) }} 
            </p>
        </div>
    </div>

    <proms-list :promos="promos" :loading="is_more"></proms-list>
    <div v-if="promos.length <= 1" class="p-8 text-gray-500 text-center">
        Promotions not set...
    </div>        

</template> 
