/// New Name: ReportList.vue
/// intended to display reports of the latest transactions in an informative way...
<script setup>
import { 
    computed, 
    toRefs 
} from 'vue'
import ReportCard from '@/components/ReportCard'

const props = defineProps({ 
    reports: Array,
    loading: Boolean,
})


const { reports: reports, loading } = toRefs(props) //the way of saving them beyond events... ?
const orderedReports = computed(() => {
    try {
        const lst = reports.value.slice().sort((a, b) => b.timestamp - a.timestamp) //hurray sorting!!!
        //console.log("PKReportList: Hello from ordering reports")//,lst.slice(0,3))
        return lst
    } catch(err) {
        return console.log("error in sorting reports: ", err)
    }
})

</script>

<template>
    <div v-if="loading" class="p-8 text-gray-500 text-center">
        Loading reports from the SpyBox...
    </div>
    <div v-else class="divide-y">
        <report-card v-for="report in orderedReports" :key="report.key" :report="report"></report-card>
    </div>
</template>
