/// Page, intended to br displayed after successful payment
/// its objective is to confirm successful transfer of JUTC on the connected wallet
<script setup>
import ReportCard from '@/components/ReportCard'
import { gettingRelevantReport, getTransferStatus } from '@/api'
import { useRoute } from "vue-router";
import { ref } from 'vue'

const DEBUG = false

const route = useRoute()
const result_msg = ref('')
const res_fetch = ref('')
const transactions = ref([])

//reading the session id sent as a :routing parameter the ':' removal
const session_id = route.params.id.substring(1)//this.$route.params

if (DEBUG) console.log("inside success page")

checkResult()

async function checkResult() {

    res_fetch.value = await getTransferStatus(session_id, DEBUG)
    if(DEBUG) console.log(`result fetched:${res_fetch.value.result} `)//with stamp:${res_fetch.value.stamp.toString()}`)
    
    switch (res_fetch.value.result) {
        case 0: 
            result_msg.value = { title: 'Problem', resultmessage: 'we experienced technical \
            problem, please check your wallet within 2 hours, if tokens not arrived please \
            contact us on admin@treasure.io'}
            break
        case 1: 
            result_msg.value = { title: 'Success', resultmessage: '  Tokens have been successfuly \
            transferred to your wallet'}
            break
        case 2:
        case 3: //in stamp: error_message
        case 4: //in stamp: original result
        default:
        result_msg.value = { title: 'Problem', resultmessage: 'system error \
        , please contact us on admin@treasure.io'}
    }    


    if (result_msg.value.title === 'Success') {
         
        if (DEBUG) console.log("success, about to display report using gettingRelevantReport(")
        
        const recent = await gettingRelevantReport(res_fetch.value.stamp)  
        transactions.value.push(recent)     
    }

}



</script>


<template>


    <div class="px-8 py-4">
        <div>
            <!-- Amount field. -->
            <div class="relative m-2 mr-4">
                <h3 class="inline font-semibold" :title="result_msg.title">                
                    {{result_msg.resultmessage}}
                </h3> 
            </div>
            <div v-if="result_msg.title !== 'Success'" class="flex items-center space-x-6 m-2 ml-auto">

                <button
                    class="text-white px-2 py-2 rounded-full font-semibold" :disabled="false"
                    :class="true ? 'bg-blue-500' : 'bg-blue-300 cursor-not-allowed'"
                    @click="checkResult"
                >
                    recheck
                </button>

            </div>
        </div>



        <!-- <card-payment :token_amount="demanded_amount"></card-payment>  -->
    </div>

    <div v-if="result_msg.title === 'Success'"  class="px-8 py-4 bg-gray-50 text-gray-500 text-center border-b">
        <report-card v-for="report in transactions" :key="report.key" :report="report">></report-card> 
    </div>
</template>