        <p>Se via monujo ne estas inter la listitaj post klako sur la butono, kaj vi scias ke ĝi funkcias kun la Solana blokĉeno, zorgeme kopiu kaj algluu vian publikan ID-on de via monujo en la flava areo sub la roz-kolora butono. 
          Se ĝi estas akceptata, tiam ĝi konvertiĝos en ion pli diskretan kaj vi povos daŭrigi kun via testa aĉeto. Se ne, via kodo ne povas esti uzata.</p>
        <h3>Testa Aĉeto</h3>
        <p>Vi povas uzi la specialan testan Visa-karton (4242 4242 4242 4242) kaj indiku estontan daton en la Koresponda Kampo. 
          Uzu vian aktivan retpoŝtan adreson se vi volas ricevi retpoŝtan konfirmon.</p>
        <p>Nia nuna sistemo estas instalita en la Solana testa reto kaj konektita al la testa Stripe-instalo. 
          Ĉio funkcias kiel se ĝi estus reala; la monmarkoj estas testaj, kaj neniu vera mono estas uzata. 
          Ĉiuj agoj estas registritaj por plia analizo, kaj ni ilin konservos en nia datumbazo kaj en la historio de Stripe-eventoj.</p>
        <h3>Lastaj minutaĵoj..</h3>
        <p>Nia sistemo estas ankoraŭ en la faza de evoluado, kaj ni ankoraŭ laboras pri ĝi. Tial bonvolu esti pacienca kun la iom malrapida 
          procezo de konektado de via elektronika monujo post premado de la roza butono.</p>
        <p>La reagordiĝa funkcio de la "Informo vidigo" funkcias nur kiam oni alvenas tien de alia vidigo... Do, se vi volas reagiordi "obstinan" konektitan monujon, pli bone estas unue iri al, ni diru, "Junga vidigo", poste iri al Informo.</p>
        <p>Por priaj problemoj pri la alfa testado, bonvolu kontakti nin ĉe alphatest@treazure.io</p>

