import { 
    gettingReports, 
    gettingJungleData,
    //Report,
 } from '@/api'
import { 
    RepDisp, 
    //JungleDisp, PromoDisp 
} from '@/models'

export const fetchReports = async () => {//async function fetchReports (): Promise<RepDisp[]|null> {
    //console.log("PK:FetchAsyncData, inside fetchReports")
    const reports = await gettingReports() //const reports:Report[]|null 
    if (reports) return reports.map((report) => new RepDisp(report))
    else return null
}

export const fetchJungleData = async () => {
    const disp_data1 = await gettingJungleData()//connection)
    return disp_data1
}
