//import { useAnchorWallet } from 'solana-wallets-vue'
import { Connection } from '@solana/web3.js'
import { ref } from 'vue'

const clusterUrl = "https://api.devnet.solana.com" //process.env.VUE_APP_CLUSTER_URL
const commitment = 'processed'
const DEBUG = false

let workspace = null
//let rpcUrl = 'http://127.0.0.1:8899' //devnet, mainnet, localnet
//let rpcUrl = 'https://api.devnet.solana.com'

export const useWorkspace = () => workspace

export const initWorkspace = () => {

    //const wallet = useAnchorWallet()
    //const connection =  new Connection(rpcUrl, 'confirmed')  
    const connection = new Connection(clusterUrl, commitment)
    const walletID = ref('')
    const buttonUsed = ref(0)
    

    function setWalletID(pkey) {
        walletID.value = pkey
        if (DEBUG) console.log('at return from setWalletID', walletID.value)
    }

    function setButtonUsed(is_it) {
        buttonUsed.value = is_it
    }

    function isConnected() {
        return (walletID.value !== '')        
    }

    workspace = {
        setWalletID,
        walletID,
        connection,
        isConnected,
        buttonUsed,
        setButtonUsed,
    }
}