const Key = [
  "Uninitialized",
  "JungleState",
  "SpyBox", 
]
const Status = [
  "NonInitialized",
  "Inactive",
  "Active",
]
const MAX_PROMO_DEPTH = 20
const MAX_REPORTS_QUEUE = 128

export class RepDisp { 
  difference
  timestamp
  instruction
  result
  label

  constructor(rawreport) { 
    //console.log("--debug, inside RepDisp constructor")
    this.difference = (Number(rawreport.tokens_at_start - rawreport.tokens_at_end))/1000000000.00
    this.timestamp =  new Number(rawreport.timestamp)
    this.instruction = HelloInstruction[rawreport.instruction]
    this.result = ActionResult[rawreport.result]
    this.label = rawreport.note //new TextDecoder().decode(rawreport.note)
  }

  get key () {
      //Att: add something random to avid ambiguity of seconds?
      return this.timestamp
  }

  get created_at () {
      return dayjs.unix(this.timestamp).format('lll') //.toString()
  }

  get created_ago () {
      return dayjs.unix(this.timestamp).fromNow() //.toString()
  }
}

export class JungleDisp {
  storage_type
  recent_report
  state
  counter_per_hour
  counter_failures
  promotions
  counter
  counter_failures
  nb_left_proms
  nb_left_reports
  nb_left_tokens
  rent_left_escrow
  rent_left_spybox
  rent_left_jungle

  constructor() {
    //console.log("--debug, inside JungleDisp constructor")
    this.storage_type = 0
    this.recent_report = 0
    this.state = 0
    this.counter_per_hour = 0 //can assess it from reports b/c of timestamps
    this.counter_failures = 0n
    this.promotions = []
    this.counter = 0n
    this.nb_left_proms = 0
    this.nb_left_reports = 0
    this.nb_left_tokens = 0n
    this.rent_left_escrow = -1 // to warn that account has to be fed with lamports
    this.rent_left_spybox = -1 // as above
    this.rent_left_jungle = -1 // as... 
  }
  static cpy(a) {
    return Object.assign(a)
  } 


  partial_get (rawjungle)  { //:JungleState
    this.storage_type = Key[rawjungle.key]
    this.state = Status[rawjungle.status]

    this.promotions =  rawjungle.promotions
    this.counter = rawjungle.counter_success
    this.counter_failures = rawjungle.counter_failure
    this.nb_left_proms = MAX_PROMO_DEPTH - rawjungle.promotions.length
    this.nb_left_reports = MAX_REPORTS_QUEUE - not_very_wise
  }
}

export class PromoDisp {
  perks
  level
  constructor(rawpromo) {
    this.perks = `${(rawpromo.perks/100).toString(10)}%`
    this.level = Number(rawpromo.level)/1000000000.00
  }
  get key () {
    //Att: add something random to avoid ambiguity in seconds?
    return this.level
  }
}


  
