/// PromsList.vue
/// intended to display promotions on the Jungle page
<script setup>
import { computed, toRefs } from 'vue'
import PromoCard from '@/components/PromoCard'

const props1 = defineProps({ 
    promos: Array,
    loading: Boolean,
})


const { promos, loading } = toRefs(props1) //the way of saving them beyond events... ?
const orderedPromotions = computed(() => {
    try {
        const lst1 = promos.value.slice().sort((a, b) => b.level - a.level) //hurray sorting!!!
        return lst1
    } catch(err) {
        return console.log("error in sorting proms: ", err)
    }
})
</script>

<template>
    <div v-if="loading" class="p-8 text-gray-500 text-center">
        Loading data from SpyBox...
    </div>

    <div v-else class="divide-y">
        <div class="px-8 py-4">
            <h3 class="inline font-semibold" >                
                Promotions set: 
            </h3>  
        </div>
        <promo-card v-for="promo in orderedPromotions" :key="promo.key" :promo1="promo"></promo-card>
    </div>
</template>
