///New Name: ReportCard.vue
///intended to display properties of Report

// save pkey as  something that can be reached from other components

//function retrievePKeyID()
// after reaching lenght of Solana public Key it
// 1. checks correctness
// 2. replaces buy shorteed version
// 3. try if it is 

 
<script setup>
import { WalletMultiButton, useWallet} from 'solana-wallets-vue' //
import { ref, watch, } from "vue" //defineProps,  defineEmits,
import { useWorkspace } from '@/composables'
import { checkWalletId } from '@/api'

const DEBUG = true

const PK_LENGTH = 44

//const returnPK = defineEmits(["process-text"]);

const { setButtonUsed, buttonUsed, setWalletID, walletID, isConnected } = useWorkspace() 
const { connected, publicKey } = useWallet()

const inputText = ref('')



watch(publicKey, () => {

  if (connected.value) {
    if (DEBUG) console.log("inside 'watch0', is the button connected?", connected.value, isConnected())
    setButtonUsed(1)
    inputText.value = ''
    //returnPK("process-text", publicKey.value)
    setWalletID(publicKey.value)
    if (DEBUG) console.log("inside 'watch3', after connecting, status:", isConnected())
  } else {
    setButtonUsed(0)
    //returnPK("process-text", '')
    setWalletID('')
    if (DEBUG) console.log("inside 'watch3', after disconnecting, is disconnected?:", !isConnected())
  }
})

const processInput = async(event) => {
  const newVal = event.target.value
  if (DEBUG) console.log("inside 'processInput0', is the ID connected?", isConnected())
  if (newVal.length >= PK_LENGTH) {
    if(await checkWalletId(newVal)) {
      const shortPK = newVal.slice(0, 4) + '..' + newVal.slice(-4)
      setButtonUsed(2)
      inputText.value = shortPK
      //returnPK("process-text", newVal)
      setWalletID(newVal)
      if (DEBUG) console.log("inside 'processInput2', after connecting, status:", isConnected())
    }
    
    // H2upwiN4Qfj38aYt8GgQREY3dyZaEoDaqjAcs9DotVr4
  } else if (newVal.length <=5) {
    setButtonUsed(0)
    //returnPK("process-text", '')
    inputText.value = ''
    setWalletID('')
    if (DEBUG) console.log("inside 'processInput3', after disconnecting, is disconnected?:", !isConnected())
  }
}

watch(walletID, () => {

  if (walletID.value == '' && buttonUsed.value == 2) {
    setWalletID('')
    if (DEBUG) console.log("inside 'watchWalletID', after clearing?:", !isConnected())
  }
})

</script>
  
<template>
    <div>
      <div v-if="buttonUsed != 2">
        <wallet-multi-button />
      </div>
      <div v-if="buttonUsed == 0">
        <small>.. or paste wallet ID below</small>
      </div>  
      <div v-if="buttonUsed != 1">
        <input class="text-black px-4 py-2 rounded-full font-semibold h-9 ml-auto" :disabled="false"
          :class="true ? 'bg-yellow-50' : 'cursor-not-allowed'" v-model.trim="inputText" @input="processInput" /> 
      </div>        
    </div>
</template>